body {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: underline;
  color: inherit;
}

a:hover {
  text-decoration: underline;
  color: #5F97FB;
}



